import Editor from "./EditorComponent";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import DOMPurify from "dompurify";
import axios from "axios";
import '../css/boardwtire.css';
const Boardupdate = () => {

  const boardID = sessionStorage.getItem("board_ID");
  const isLogin = sessionStorage.getItem("isLogin");
  const [desc, setDesc] = useState("");
  const [title, setTitle] = useState("");
  const [tags, setTags] = useState([]);
  const [tagInput, setTagInput] = useState('');
  const [tagError, setTagError] = useState('');
  const [posts_option, setPosts_option] = useState('');
  const handleTagInputChange = (e) => {
    setTagInput(e.target.value);
};

const handleTagInputKeyDown = (e) => {
  if (e.key === 'Enter') {
  e.preventDefault();
  const trimmedTag = tagInput.trim();
  const trtag = trimmedTag.toUpperCase();
  if (trimmedTag === '') {
      setTagError('태그를 입력하세요.');
      return;
  }
  if (tags.includes(trtag)) {
      alert('이미 있는 태그입니다. 다시 입력하세요.');
      setTagInput('');
      return;
  }
  setTags((prevTags) => [...prevTags, trtag]);
  setTagInput('');
  setTagError('');
  }
};


const handleTagRemove = (tag) => {
    setTags((prevTags) => prevTags.filter((t) => t !== tag));
};

const navigate = useNavigate();
function onEditorChange(value) {
    setDesc(value)
}

const imgs = (imgSrc) =>{
  const img = new Image();

  img.src = imgSrc;

  if(img.width > 5000 || img.height > 5000){
    return true
  }
  return false
}
  useEffect(() => {
    axios
      .post("/api/board/update", {
        boardID,
        isLogin,
        give: true,
      })
      .then((res) => {
        if (res.data.success) {
          setTitle(res.data.data[0].title);
          setDesc(res.data.data[0].content);
          setTags(res.data.tagNames)
          setPosts_option(res.data.data[0].posts_option)
        } else {
          alert(res.data.message);
          navigate("/");
        }
      })
      .catch((err) => console.log(err.message));
  }, [boardID, isLogin, navigate]);

  function onEditorChange(value) {
    setDesc(value);
  }

  function handleSubmit() {
    const sanitizedDesc = DOMPurify.sanitize(desc);
    const sanitizedTitle = DOMPurify.sanitize(title);
    const isLogin = sessionStorage.getItem("isLogin");
    const boardID = sessionStorage.getItem("board_ID");

    const tempEl = document.createElement("div");
    tempEl.innerHTML = sanitizedDesc;
    const firstImg = tempEl.querySelector("img");
    const imgSrc = firstImg ? firstImg.getAttribute("src") : "";

    axios
      .post("/api/board/update", {
        title: sanitizedTitle,
        content: sanitizedDesc,
        image: imgSrc,
        isLogin: isLogin,
        boardID: boardID,
        tags: tags,
        posts_option: posts_option,
        give: false,
      })
      .then((res) => {
        if (res.data.success) {
          alert("업데이트 완료");
          sessionStorage.setItem("board_ID", "");
          navigate("/");
        } else {
          alert(res.data.message);
        }
      })
      .catch((err) => console.log(err.message));
  }

  return (
    <>
    <div className="boardwrite">
            <div className="wtireContainer">
                <form className="form" onSubmit={handleSubmit}>
                    <div className="form-group">
                      <input type="text" value={title} placeholder="제목을 입력하세요."
                        onChange={(e) => setTitle(e.target.value)}
                        className="title-input"
                      />
                    </div>  
                    <div className='from-group'>
                      <label id='label'>게시판을 선택해주세요: </label>
                      <select name="boards" id='selete' value={posts_option} onChange={(e)=>{setPosts_option(e.target.value)}}>
                        <option value=""> -- 게시판 선택 -- </option>
                        <option value="1"> 잡담 게시판 </option>
                        <option value="2"> 프로젝트 게시판 </option>
                      </select>
                    </div>
                    <div className="editor-wrapper">
                      <Editor value={desc} onChange={onEditorChange} />
                    </div>
                    <div className="form-group">
                        <div className="tag-input-container">
                        {tags.map((tag) => (
                            <div key={tag} className="tag">
                                <span>#{tag}</span>
                                <button type="button" className="remove-tag"
                                    onClick={() => handleTagRemove(tag)}>&times;
                                </button>
                            </div>
                        ))}
                        </div>
                        <div className='tagIputGroup'>
                            <input type="text" id="tags" required value={tagInput}
                                onChange={handleTagInputChange}
                                onKeyDown={handleTagInputKeyDown}
                            />      
                            <span class="highlight"></span>
                            <span class="bar"></span>
                            <label className='label' htmlFor="tags"> #태그 :</label>  
                        </div>
                    </div>
                    <div className="form-group">
                        <button className="submit-btn" onClick={handleSubmit}>게시하기</button>
                    </div>
                </form>
            </div>
        </div>
    </>
  )
};

export default Boardupdate;
