import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import '../../css/CSSadmin/InquiryList.css'; // CSS 파일 경로
import axios from 'axios';
import NewNav from "./adminNav2";

const InquiryList = () => {
  const admin_id = sessionStorage.getItem('isLogin');
  const [inquiries, setInquiries] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const navigate = useNavigate();

  useEffect(() => {
    fetchInquiries();
  }, []);

  const fetchInquiries = () => {
    axios.post('/api/admin/inquiry', { admin_id })
      .then((res) => {
        if (res.data.success) {
          setInquiries(res.data.list);
          const totalPagesCount = Math.ceil(res.data.list.length / 10);
          setTotalPages(totalPagesCount);
        } else {
          alert(res.data.message);
          navigate('/');
        }
      })
      .catch((error) => {
        console.error('문의 목록을 불러오는 중 에러 발생:', error);
      });
  };

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const getPageInquiries = () => {
    const startIndex = (currentPage - 1) * 10;
    const endIndex = startIndex + 10;
    return inquiries.slice(startIndex, endIndex);
  };

  return (
    <div className="inquiry-list-container">
      <NewNav/>
      <h1>문의 목록</h1>
      <table className="inquiry-table">
        <tbody>
          {getPageInquiries().map((inquiry) => (
            <tr key={inquiry.id}>
              <td>
                <Link to={`/admin/ReplyPage/${inquiry.id}`}>{inquiry.title}</Link>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <div className="pagination">
        {Array.from({ length: totalPages }, (_, index) => (
          <button
            key={index + 1}
            onClick={() => handlePageChange(index + 1)}
            className={currentPage === index + 1 ? 'active' : ''}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
};

export default InquiryList;
