import axios from "axios";
import useSWR from 'swr'
import { Link, useParams } from "react-router-dom";
import '../css/editorStyles.css'
import { useState, useEffect,useRef } from "react";
import { fetcher } from "../utils/fetcher";
import { useNavigate } from 'react-router-dom';
import HashTagPost from './HashTagPost';
import Comment from './comment';
const BoardDetail = () => {
  const DetailRef = useRef(null);
  const { id } = useParams();
  const { data, error, isLoading } = useSWR(`/api/board/${id}`, fetcher)
  const navigate = useNavigate();
  const [isLiked, setIsLiked] = useState(false);
  const [isFixed, setIsFixed] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [isDetail,setIsDetail] = useState(false);
  const [user_id, setUser_id] = useState("");
  const [uid, setUid] = useState('');
  const [hashtags, setHashtags] = useState([]);
  const idx_id = sessionStorage.getItem("isLogin");
  const goods = () =>{
    const userid = sessionStorage.getItem("isLogin");

    axios({
      method: 'post',
      url: '/api/board/good',
      data: {
        userid,
        id
      }
    })
    .then((res) =>{
      if(res.data.success) {
      } else {
        alert(res.data.message);
        setIsLiked(false);
      }})
      .catch(err => console.log(err.message));

  }

  const update = () =>{
    sessionStorage.setItem("board_ID",id);
    navigate("/boardupdate");
  }

  const deleteBoard = () =>{
    axios.post("/api/board/delete",{
      BoardID: id,
    }).then((res)=>{
      if(res.data.success){
        alert(res.data.message);
        navigate('/');
      }else{
        alert(res.data.message);
        navigate('/');
      }
    })
  }

  const handleLikeClick = () => {
    setIsLiked(!isLiked);
    goods();
  };
  const idchk = () => {
    if(data){
      axios.post("/api/board/detail",{
        id: data.data.user_id,
        board_id: id
      }).then((res) => {
        if(res.data.success){
          setUser_id(res.data.ids);
          setHashtags(res.data.tag);
          setUid(res.data.userId);
        }
      })
    }
  }

useEffect(()=>{
  const handleOutsideClick = (event) => {
    if (DetailRef.current && !DetailRef.current.contains(event.target)) {
      setIsDetail(false);
    }
  };

  document.addEventListener('mousedown', handleOutsideClick);

  return () => {
    document.removeEventListener('mousedown', handleOutsideClick);
  };
},[DetailRef])

  useEffect(() => {
    idchk();

  },[data])

  useEffect(() => {
    window.scrollTo(0,0);

    const handleScroll = () => {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      setIsFixed(scrollTop > 100);
    };

    const handleResize = () => {
      setIsMobile(window.innerWidth < 800);
    };

    window.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', handleResize);
    
    handleResize();
    
    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  useEffect(() => {
    axios.post("/api/board/isLike", {
      isLogin: idx_id,
      board_id: id
    }).then((res) =>{
      if(res.data.success){
        setIsLiked(res.data.likes);
      }
    })
  },[])
  const date = (d) => {
    const dateString = d;
    const dateObject = new Date(dateString);
    
    const year = dateObject.getUTCFullYear();
    const month = dateObject.getUTCMonth() + 1; // 월은 0부터 시작하므로 +1 해줍니다.
    const day = dateObject.getUTCDate();
    const dates = `${year}-${month}-${day}`;

    return dates;
    
  }

  const report = (id, isPost) => {
    const userId = sessionStorage.getItem('isLogin');
    if(!userId){
      return alert('로그인후 이용가능합니다')
    }
    if (isPost !== 1) { // 수정된 부분
      isPost = false; // 수정된 부분
    }
    axios.post('/api/reports', {
      user_id: userId,
      id,
      isPost
    }).then((res) => {
      if (res.data.success) {
        alert('신고 되었습니다');
      }
    });
  };
  
  if (error) {
    return <>error</>;
  }
  if (isLoading) {
    return (<>loading</>);
  }
  if (!data) {
    return <>No data found</>;
  }
  return (
    <div className='dtl-wrap'>
      <h1 style={{
        whiteSpace: 'nowrap'
      }}>{data.data.title}</h1>
      <div ref={DetailRef}>
      <div className="rightBtn" onClick={()=>{
        setIsDetail(!isDetail)
      }}>
        <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 1024 1024" height="100%" width="100%" xmlns="http://www.w3.org/2000/svg"><path d="M456 231a56 56 0 1 0 112 0 56 56 0 1 0-112 0zm0 280a56 56 0 1 0 112 0 56 56 0 1 0-112 0zm0 280a56 56 0 1 0 112 0 56 56 0 1 0-112 0z"></path></svg>
      </div>
      {
        parseInt(idx_id) === data.data.user_id ?

        <div className={isDetail ? 'updateDeleteBtn' : 'updateDeleteBtnnone'}>
          <button onClick={update} className="updateBtn">수정</button> 
          <button onClick={deleteBoard} className="updateBtn">삭제</button>
        </div>
        :
        <div className={isDetail ? 'updateDeleteBtn' : 'updateDeleteBtnnone'}>
          <button onClick={()=>{
            report(id,1)
          }} className="updateBtn">신고</button> 
        </div>
      }
      </div>
      <hr/>
      {!isMobile && (
        <div className={`gdwrap ${isLiked ? 'liked' : ''} ${isFixed ? 'fixed' : ''}`}
          style={{
            display: isMobile ? 'none' : 'block',
            position: isFixed ? 'fixed' : 'absolute',
            left: '14%',
            top: isFixed ? '185px' : '40%', 
          }}
        >
          <div className={`good ${isLiked ? 'liked' : ''}`} onClick={handleLikeClick}>
            <svg viewBox='0 0 24 24' style={{ marginLeft: '6.5px', marginTop: '8px', width: '26px', height: '26px' }}>
              <path fill={isLiked ? 'red' : 'currentColor'} d='M18 1l-6 4-6-4-6 5v7l12 10 12-10v-7z'></path>
            </svg>
          </div>
        </div>
      )}
      <div className='u-b'>
        <p className='data_u'>
          <Link to={`/profile/${uid}`} style={{color: 'black'}}>{user_id}</Link> <span id='wrtD'>{date(data.data.updated_at)}</span>
          {isMobile && (
            <span className={`smallgood ${isLiked ? 'liked' : ''}`} onClick={handleLikeClick}>
              좋아요
              <svg viewBox='0 0 24 24' style={{ marginLeft: '3px', marginTop: '11px', width: '10px', height: '10px' }}>
                <path fill={isLiked ? 'red' : 'currentColor'} d='M18 1l-6 4-6-4-6 5v7l12 10 12-10v-7z'></path>
              </svg>
            </span>
          )}
        </p>
        <div className='hashTag'>
          <HashTagPost hashtags={hashtags} />
        </div>
      </div>
      <div  className="contents" style={{
        width: "900px",
        display: "flex",
        justifyContent: "center"
      }}>
        <div style={{
          marginTop: 20,  
          minHeight:  "390px",
          width: "100%",
          maxWidth: "1000px",
          borderLeft: "1px solid gray",
          borderRight: "1px solid gray",
          overflowX: "auto"
        }} dangerouslySetInnerHTML={{__html:data.data.content}}></div>
      </div>
      <div style={{
        height: "30px"
      }}></div>
        <Comment />
      <style jsx>{`
        .dtl-wrap {
          max-width: 60%;
          margin: 0 auto;
          margin-top: 100px;
          display: flex;
          flex-direction: column;
          align-items: center;
        }
        .rightBtn{
          width: 35px;
          height: 35px;
          position: absolute; 
          right: 300px;
          cursor: pointer;
        }
        .updateDeleteBtn{
          width: 150px;
          height: 50px;
          position: absolute; 
          right: 330px;
          border: 2px solid black;
          border-radius: 4px;
          display: flex;
          justify-content: space-around;
          align-items: center;
        }
        .updateDeleteBtnnone{
          display: none;
        }
        .updateBtn{
          background-color: #a428db;
          color: white;
          padding: 8px 16px;
          border: none;
          border-radius: 4px;
          font-size: 14px;
          cursor: pointer;
        }
        .updateBtn:hover{
          background-color: #7d1ac9;
        }
        .rightBtn svg:hover{
          color: black;
        }
        .data_u {
          margin-left: 10%;
          display: flex;
          align-items: center;
          width: 100%;
        }
        .u-b{
          width: 100%;
        }
        #wrtD {
          font-size: 12px;
          margin: 2px 0 0 10px;
        }
        .hashTag {
          padding: 20px;
          margin-left: 9%;
        }
        .good {
          width: 40px;
          height: 40px;
          border-radius: 20px;
          border: 1px solid gray;
        }
        .good.fixed { position: fixed; }
        .smallgood {
          font-size: 12px;
          margin-top: -7px;
          margin-left: 50%;
          font-weight: bold;
          cursor: pointer;
        }
        path:hover { fill: red; }
        path:visited { fill: red; }
      `}</style>
    </div>
  );
  
};

export default BoardDetail;
