import "../../css/rcm.css";
import { useState, useEffect, useRef } from 'react';
import { Link } from "react-router-dom";
import axios from "axios";
export const Rcm = () => {
  const [boardList, setBoardList] = useState([]);
  const [boardList1, setBoardList1] = useState([]);
  const [slideIndex, setSlideIndex] = useState(0);
  const [slideWidth, setSlideWidth] = useState(0);
  const [slideMargin, setSlideMargin] = useState(0);
  const slideElement = useRef()
  const slideCount = document.querySelectorAll('.slide').length;
  const visibleSlideCount = 4;
  const notimg = 'data:image/png;base64,';

  useEffect(() => {
    axios 
      .get("/api/board/popular") 
      .then((res) => {
        setBoardList(res.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []); 
 

  useEffect(() => {
    const slider = document.querySelector('.slider');
    slider.style.transform = `translateX(-${slideIndex * (slideWidth + slideMargin)}px)`;
  }, [slideIndex, slideWidth, slideMargin]);

  const nextButtonHandler = () => {
    if(slideWidth === 0) {
      setSlideWidth(document.querySelector('.slide').clientWidth);
      setSlideMargin(parseInt(getComputedStyle(document.querySelector('.slide')).marginRight));
    }
    const maxIndex = slideCount - visibleSlideCount;
    if (slideIndex < maxIndex) {
      setSlideIndex(Math.min(slideIndex + 3, maxIndex));
    }
  };

  const prevButtonHandler = () => {
    if(slideIndex === 2){
      setSlideIndex(0);
    }else if(slideIndex === 1){
      setSlideIndex(0);
    }else{
      setSlideIndex(slideIndex - 3);
    }
  };

   //최근 콘텐츠
  const [slideIndex1, setSlideIndex1] = useState(0);
  const [slideWidth1, setSlideWidth1] = useState(0);
  const [slideMargin1, setSlideMargin1] = useState(0);
  const slideCount1 = document.querySelectorAll('.slide1').length;
  const visibleSlideCount1 = 4;

  useEffect(() => {
    axios 
      .get("/api/board/latest") 
      .then((res) => {
        setBoardList1(res.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []); 


  useEffect(() => {
    const slider = document.querySelector('.slider1');
    slider.style.transform = `translateX(-${slideIndex1 * (slideWidth1 + slideMargin1)}px)`;
  }, [slideIndex1, slideWidth1, slideMargin1]);


  const nextButtonHandler1 = () =>  {
    if(slideWidth1 === 0) {
      if(slideWidth1 === 0) {
        setSlideWidth1(document.querySelector('.slide1').clientWidth);
        setSlideMargin1(parseInt(getComputedStyle(document.querySelector('.slide1')).marginRight));
      }
    }
    const maxIndex = slideCount1 - visibleSlideCount1;
    if (slideIndex1 < maxIndex) {
      setSlideIndex1(Math.min(slideIndex1 + 3, maxIndex));
    } 
  };


  const prevButtonHandler1 = () => {
    if (slideIndex1 > 0) {
      if(slideIndex1 === 2){
        setSlideIndex1(0);
      }else if(slideIndex1 === 1){
        setSlideIndex1(0);
      }else{
        setSlideIndex1(slideIndex1 - 3);
      }
    } 
    
  }; 


const times = (time) =>{

  const now = new Date();


  const givenTime = new Date(time);


  const timeDiff = now - givenTime;


  const msPerSecond = 1000;
  const msPerMinute = msPerSecond * 60;
  const msPerHour = msPerMinute * 60;
  const msPerDay = msPerHour * 24;
  const msPerMonth = msPerDay * 30; 

  let result;

  if (timeDiff < msPerMinute) {
  // 1분 미만인 경우
  result = '방금 전';
  } else if (timeDiff < msPerHour) {
  // 1시간 미만인 경우
  const minutes = Math.floor(timeDiff / msPerMinute);
  result = `${minutes}분 전`;
  } else if (timeDiff < msPerDay) {
  // 1일 미만인 경우
  const hours = Math.floor(timeDiff / msPerHour);
  result = `${hours}시간 전`;
  } else if (timeDiff < msPerMonth) {
  // 1달 미만인 경우
  const days = Math.floor(timeDiff / msPerDay);
  result = `${days}일 전`;
  } else {
  // 1달 이상인 경우
  const months = Math.floor(timeDiff / msPerMonth);
  result = `${months}개월 전`;
  }
  return result;
}
  return ( 
    <div>
      <div className="rcm-container">
        <div className="rcm-text">추천 항목...</div>
      </div>
      <div className="slider-container">
        <div className="slider">
          {boardList.map((board) => {
            return(  
              <div className="post-item" key={board.id}>
                <div className="post-thumbnail">
                <Link to={`/board/${board.id}`} id="boardTD">
                  
                <div className="slide" ref={slideElement}
                style={{
                  backgroundImage: `url(${board.image ? board.image : notimg})`,
                }}/>
                <div className="post-title">{board.title}</div>
                <span className="post-author"><Link to={`/profile/${board.user_id}`}>{board.username}</Link></span>-
                <span className="post-date">{times(board.updated_at)}</span>
                </Link>
              </div>
                </div>
            );
          })}

        </div>
        <button className="prev-button" onClick={prevButtonHandler}></button>
        <button className="next-button" onClick={nextButtonHandler}></button>
      </div>

      <div className="rcm-container1">
        <div className="rcm-text1">최근 콘텐츠</div>
      </div>
      <div className="slider-container1">
        
        <div className="slider1">
        {boardList1.map((board) => {
            return(  
              <div className="post-item" key={board.id}>
                <div className="post-thumbnail">
                <Link to={`/board/${board.id}`} id="boardTD">
                  
                <div className="slide1" ref={slideElement}
                style={{
                  backgroundImage: `url(${board.image ? board.image : notimg})`,
                }}/>
                <div className="post-title">{board.title}</div>
                <span className="post-author"><Link to={`/profile/${board.user_id}`}>{board.username}</Link></span>-
                <span className="post-date">{times(board.updated_at)}</span>
                </Link>
              </div>
                </div>
            );
          })}
        </div>
        <button className="prev-button1" onClick={prevButtonHandler1}></button>
        <button className="next-button1" onClick={nextButtonHandler1}></button>
      </div>
    </div>
  );
};

export default Rcm;