import React, { useState,useEffect } from 'react';
import { useParams,useNavigate } from 'react-router-dom';
import '../../css/CSSadmin/ReplyPage.css';
import axios from 'axios';
import NewNav from "./adminNav2";
const ReplyPage = () => {
  const admin_id = sessionStorage.getItem('isLogin');
  const navigate = useNavigate();
  const { id } = useParams();
  const [replyText, setReplyText] = useState('');
  const [list, setList] = useState([]);
  const [userEmail, setUserEmail] = useState('');
  const handleReplySubmit = () => {
    axios.post('/api/admin/inquirySubmit',{
      userEmail,
      replyText,
      id
    }).then((res)=>{
      if(res.data.success){
        alert('답변완료')
        navigate('/admin')
      }
    })
  };

  const handleReplyChange = (event) => {
    setReplyText(event.target.value);
  };

  useEffect(()=>{
    axios.post('/api/admin/inquiryDetail',{
      id,
      admin_id
    }).then((res)=>{
      if(res.data.success){
        setList(res.data.list);
        setUserEmail(res.data.list[0].email)
      }else{
        alert(res.data.message);
        navigate('/');
      }
    })
  },[])

  return (
    <div className="reply-page-container">
      <NewNav/>
      {
        list.length > 0 ?
        <div>
              <h1>문의 답변 페이지</h1>
          <p>문의 ID: {id}</p>

          <div>
            <div>
              이름: {list[0].username}<br/>
              이메일:{list[0].email}
            </div>
            <br/>
            <br/>
            <div>
            제목:<label style={{
              fontSize: '20px'
            }}> {list[0].title}</label><br/>
              내용: {list[0].content}
            </div>
          </div>
          <div className="reply-input-container">
            <textarea
              className="reply-textarea"
              value={replyText}
              onChange={handleReplyChange}
              placeholder="답변 내용을 입력하세요..."
            ></textarea>

            <button className="reply-submit-button" onClick={handleReplySubmit}>
              답변 제출
            </button>
          </div>
        </div>
        :
        <div>
          loding
        </div>
      }
      
    </div>
  );
};

export default ReplyPage;
