import { useState, useEffect } from "react";
import "../../css/my.css";
import axios from "axios";
import { Link } from "react-router-dom";
export const My = () => {

    const [click, setClick] = useState('p');
    const [click1, setClick1] = useState('p1');
    const [board, setBoard] = useState([]);
    const notimg = 'data:image/png;base64,';
    useEffect(()=>{
        const isLogin = parseInt(sessionStorage.getItem('isLogin'));
        axios.get("/api/board/myContents", {
            params: {
              isLogin: isLogin
            }
          })
        .then((res) =>{ 
            if(res.data.success){
                setBoard(res.data.data);
                
            }
        })
    }, [])

    const none = () =>{
        if(click === 'p'){
            setClick('container');
        }else{
            setClick('p');
        }
        
        if(click1 === 'p1'){
            setClick1('container1');
        }else{
            setClick1('p1');
        }
    }

    const times = (time) =>{

        const now = new Date();


        const givenTime = new Date(time);


        const timeDiff = now - givenTime;


        const msPerSecond = 1000;
        const msPerMinute = msPerSecond * 60;
        const msPerHour = msPerMinute * 60;
        const msPerDay = msPerHour * 24;
        const msPerMonth = msPerDay * 30; 

        let result;

        if (timeDiff < msPerMinute) {
        // 1분 미만인 경우
        result = '방금 전';
        } else if (timeDiff < msPerHour) {
        // 1시간 미만인 경우
        const minutes = Math.floor(timeDiff / msPerMinute);
        result = `${minutes}분 전`;
        } else if (timeDiff < msPerDay) {
        // 1일 미만인 경우
        const hours = Math.floor(timeDiff / msPerHour);
        result = `${hours}시간 전`;
        } else if (timeDiff < msPerMonth) {
        // 1달 미만인 경우
        const days = Math.floor(timeDiff / msPerDay);
        result = `${days}일 전`;
        } else {
        // 1달 이상인 경우
        const months = Math.floor(timeDiff / msPerMonth);
        result = `${months}개월 전`;
        }
        return result;
    }



    return(
        <div>
            <div className="my-container1">
                <div className="rcm-text1">내 콘텐츠</div>
                <div className="rcm-bUtton" onClick={none}><svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 512 512" height="100%" width="100%" xmlns="http://www.w3.org/2000/svg"><path d="M64 144a48 48 0 1 0 0-96 48 48 0 1 0 0 96zM192 64c-17.7 0-32 14.3-32 32s14.3 32 32 32H480c17.7 0 32-14.3 32-32s-14.3-32-32-32H192zm0 160c-17.7 0-32 14.3-32 32s14.3 32 32 32H480c17.7 0 32-14.3 32-32s-14.3-32-32-32H192zm0 160c-17.7 0-32 14.3-32 32s14.3 32 32 32H480c17.7 0 32-14.3 32-32s-14.3-32-32-32H192zM64 464a48 48 0 1 0 0-96 48 48 0 1 0 0 96zm48-208a48 48 0 1 0 -96 0 48 48 0 1 0 96 0z"></path></svg></div>
            </div>
            <div className={click}>
            <div className="nmp">
                <div className="el">제목</div>
                <div className="mb">이름</div>
                <div className="uh">유형</div>
                <div className="pj">편집</div>
            </div>
            {
                board.length === 0  ?
                <div className="postCheck">
                콘텐츠가 없습니다
                </div>
                :
                board.map((board)=>{
                    return(
                        <div className="postCheck">
                            <Link to={`/board/${board.id}`}>
                            <div className="post-el">
                                <div className="post-img" style={{
                                backgroundImage: `url(${board.image ? board.image : notimg})`, 
                                border: `1px black solid`, 
                                backgroundRepeat: "no-repeat",
                                backgroundSize: `auto 100%`,
                                backgroundPosition: 'center'}}
                                ></div> 
                                <div className="post-text"><label style={{  
                                    whiteSpace: "nowrap",
                                    overflow: "hidden",
                                    textOverflow: "ellipsis"
                                }}>{board.title}</label></div>
                            </div>
                            </Link>
                            <div className="post-mb">{board.username}</div>
                            <div className="post-uh">프로젝트</div>
                            <div className="post-pj">{times(board.updated_at)}</div>
                            
                        </div>
                    )
                })
            }
            
            </div>
            <div className={click1}>
                <div className="post2-container">
                    {
                        board.map((board)=>{
                            return(
                                <Link to={`/board/${board.id}`}>
                                <div className="post2" style={{
                                    border: 'none',
                                    color: "black"
                                    }}>
                                    <div style={{
                                        width: '100%',
                                        height: '80%',
                                        backgroundImage: `url(${board.image ? board.image : notimg})`, 
                                        border: `1px black solid`, 
                                        backgroundRepeat: "no-repeat",
                                        backgroundSize: `auto 100%`,
                                        backgroundPosition: 'center'}}></div>
                                    <div className="myboardTitle">{board.title}</div>
                                </div>
                                </Link>
                            )
                        })
                    }
                    
                </div>
            </div>
        </div>
    )
}