import React from 'react';
import { useParams } from 'react-router-dom';
import '../../css/CSSadmin/ReportDetailPage.css';
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import NewNav from "./adminNav2";
const ReportDetailPage = () => {
  const { id } = useParams();

  const Navigate = useNavigate();
  const [reports,setreports] = useState([]);
  
  let report_type;
  const admin_id = sessionStorage.getItem('isLogin');
  useEffect(()=>{
    axios.post('/api/admin/reports/detail',{
      id: id,
      admin_id: admin_id
    })
    .then((res)=>{
      if(res.data.success){
        setreports(res.data.reports);
      }else{
        alert('비정상적인 집근입니다');
        Navigate('/');
      }
    })
  },[]);

  

  const reportType = (post_id, comment_id) => {
    if(post_id !== null){
      report_type = "게시물";
      return report_type;
    }
    else{
      report_type = "댓글";
      return report_type;
    }
  };

  const reportTypeId = (post_id, comment_id) => {
    if(post_id !== null){
      return `게시물 아이디 : ${post_id}`;
    }
    else{
      return `댓글 아이디 : ${comment_id}`;
    }
  };

  const reportTypeDelete = (post_id, comment_id) => {
    if(post_id !== null){
      return `게시물삭제`;
    }
    else{
      return `댓글삭제`;
    }
  };

  const reportDelete = () => {
    axios.post('http://localhost:8000/api/admin/reports/delete',{
      id: id,
      admin_id: admin_id,
      post_id : reports[0].post_id,
      comment_id : reports[0].comment_id

    })
    .then((res)=>{
      if(res.data.success){
        alert('삭제되었습니다.');
        Navigate('/');
      }else{
        alert('비정상적인 집근입니다');
        Navigate('/');
      }
    })
  };


  return (
    <div className="report-detail-container">
      <NewNav/>
      <h1>신고 세부 정보</h1>
      <div className="report-card">
      {reports.length > 0 ? (
  <div>
    <h2>신고 유형: {reportType(reports[0].post_id, reports[0].comment_id)}</h2>
    <p>신고자 아이디: {reports[0].user_id}</p>
    <p>{reportTypeId(reports[0].post_id, reports[0].comment_id)}</p>
    <button onClick={reportDelete}>{reportTypeDelete(reports[0].post_id, reports[0].comment_id)}</button>
  </div>
) : (
  <p>Loading...</p>
)}
      </div>
    </div>
  );
};

export default ReportDetailPage;
