import axios from 'axios';
import React, { useState, useRef, useEffect } from 'react';

const ProfilePage = () => {
  const [selectedImage, setSelectedImage] = useState(null);
  const [uploadedImage, setUploadedImage] = useState(null);
  const [defaultImageSrc, setDefaultImageSrc] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [newUsername, setNewUsername] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [isEditingUsername, setIsEditingUsername] = useState(false);
  const [isEditingPassword, setIsEditingPassword] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const fileInputRef = useRef(null);
  const usernameInputRef = useRef(null);
  const passwordInputRef = useRef(null);
  const user_id = sessionStorage.getItem("isLogin");
  const defaultImg = 'data:image/png;base64,';
  useEffect(() => {
    axios.post('/api/profile/get',{
      user_id
    }).then((res)=>{
      if(res.data.success){
        setUsername(res.data.data.username);
        setPassword(res.data.data.password);
        setDefaultImageSrc(res.data.data.profile);
        if(res.data.data.profile === null){
          setDefaultImageSrc(defaultImg)
        }
      }
    })
  },[])
  useEffect(() => {
    if (isEditingUsername) usernameInputRef.current.focus();
    else if (isEditingPassword) passwordInputRef.current.focus();

  }, [isEditingUsername, isEditingPassword]);

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.onload = () => {
      const imageData =reader.result;
      setUploadedImage(imageData);
      setDefaultImageSrc('');
      setSelectedImage(null)
      axios.post('/api/profile/upload',{
        user_id,
        imageData,
        isUpdate: 'img'
      })
    };
  
    reader.readAsDataURL(file);
  };

  const handleImageUpload = () => { fileInputRef.current.click(); };

  const handleImageRemove = () => {
    setSelectedImage(null);
    setUploadedImage(null);
    setDefaultImageSrc(defaultImg);
    axios.post('/api/profile/upload',{
      user_id,
      imageData: null,
      isUpdate: 'img'
    })
  };

  const handleUsernameEditClick = () => { setIsEditingUsername(true); };

  const handleUsernameSaveClick = () => {
    if (newUsername.trim() === '') {
      alert('아이디를 입력해주세요.');
      usernameInputRef.current.focus();
    }else if (newUsername.length < 3) {
      alert('아이디는 최소 3자 이상이어야 합니다.');
      usernameInputRef.current.focus();
    }else if (newUsername !== username) {
      setUsername(newUsername);
      setIsEditingUsername(false);

      axios.post('/api/profile/upload',{
        user_id,
        id: newUsername,
        isUpdate: 'id'
      })
    }
    setNewUsername('');
  };

  const handlePasswordSaveClick = () => {
    if(newPassword.trim() === '') {
      alert('비밀번호를 입력해주세요.');
      passwordInputRef.current.focus();
    }else if (newPassword.length < 9) {
      alert('비밀번호는 최소 9자 이상이어야 합니다.');
      passwordInputRef.current.focus();
    }else if (newPassword !== password) {
      setPassword(newPassword);
      setIsEditingPassword(false);
      setShowPassword(false);
      axios.post('/api/profile/upload',{
        user_id,
        pw: newPassword,
        isUpdate: 'pw'
      })
    }
    setNewPassword('');
  };

  const handleUsernameInputChange = (e) => { setNewUsername(e.target.value); };
  const handlePasswordInputChange = (e) => { setNewPassword(e.target.value); };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      if (isEditingUsername) {
        handleUsernameSaveClick();
        usernameInputRef.current.focus();
      }else if (isEditingPassword) {
        handlePasswordSaveClick();
        passwordInputRef.current.focus();
      }
    }
  };

  const handleUserInputFocus = () => {
    if(newUsername.length < 3 && newUsername.trim() !== '') {
      alert('아이디는 최소 4자 이상이어야 합니다.');
      usernameInputRef.current.focus();
    }else if (newPassword.length < 9 && newPassword.trim() !== '') {
      alert('비밀번호는 최소 10자 이상이어야 합니다.');
      passwordInputRef.current.focus();
    }
  };

  const handlePasswordEditClick = () => {
    setIsEditingPassword(true);
    setShowPassword((prevShowPassword) => !prevShowPassword);
  };

  return (
    <>
      <div className="profile-container">
        <div className="profile-image">
          <div className="profile-image-preview"
            style={{ backgroundImage: `url(${selectedImage || uploadedImage || defaultImageSrc})` }}
          />
          <div className="image-actions">
            <input type="file" accept="image/*" ref={fileInputRef} onChange={handleImageChange} style={{ display: 'none' }}/>
            <div>
              <button className='addimg' onClick={handleImageUpload}>이미지 업로드</button>
              <div className="removeimg" onClick={handleImageRemove}>이미지 제거</div>
            </div>
          </div>
        </div>
        <div className="profile-info">
          <div className="input-group">
            <label style={{ fontWeight: 'bold', width: '80px', textAlign: 'center',fontSize:'17px',
              marginRight: `${isEditingUsername ? '19px' : '35px'}`
            }}>아이디:</label>
            {isEditingUsername ? (
              <div className='savewrap'>
                <input type="text" id="username" value={newUsername}
                  onChange={handleUsernameInputChange}
                  onKeyPress={handleKeyPress}
                  onFocus={handleUserInputFocus}
                  ref={usernameInputRef}
                />
                <button onClick={handleUsernameSaveClick}>저장</button>
              </div>
            ) : (
              <div className="display-group">
                <span style={{ width: '200px', overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis',
                }}>{username}</span>
                <button onClick={handleUsernameEditClick}>수정</button>
              </div>
            )}
          </div>
          <div className="input-group">
            <label style={{ fontWeight: 'bold', width: '80px', textAlign: 'center',fontSize:'17px',
              marginRight: `${isEditingPassword ? '19px' : '35px'}`
            }}>비밀번호:</label>
            {isEditingPassword ? (
              <div className='savewrap'>
                <input  id="password" value={newPassword}
                  type={showPassword ? 'text' : 'password'}
                  onChange={handlePasswordInputChange}
                  onKeyPress={handleKeyPress}
                  onFocus={handleUserInputFocus}
                  ref={passwordInputRef}
                />
                <button onClick={handlePasswordSaveClick}>저장</button>
              </div>
            ) : (
              <div className="display-group">
                <span style={{ 
                  width: '200px', overflow: 'hidden', whiteSpace: 'nowrap',
                  textOverflow: 'ellip',  WebkitTextSecurity: 'disc',  textSecurity: 'disc'
                }}>{password}</span>
                <button onClick={handlePasswordEditClick}>수정</button>
              </div>
            )}
          </div>
        </div>
      </div>
      <style jsx>{`
        .profile-container {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          padding: 20px;
          margin-top: 100px;
          background-color: #f8f8f8;
        }

        .profile-image-preview {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          background-size: cover;
          background-position: center;
        }

        .image-actions {
          display: flex;
          justify-content: center;
          margin-top: 30px;
        }

        .image-actions button { margin: 0 5px; }

        .removeimg{
          margin-top:10px;
          text-align:center;
          padding: 10px 19px;
          border-radius: 4px;
          cursor:pointer;
          color:#a428db;
        }
        .removeimg:hover{
          color:#7d1ac9;
          background:#eee;
        }
        .removeimg::after{
          content:' ';
          position:absolute;
          border-bottom:1px solid #000;
          width:90px;
          margin-left:-88px;
          margin-top:18px;
          height:1px;
          opacity:0.5;
        }

        .profile-info {
          display: flex;
          flex-direction: column;
          align-items: center;
        }

        .input-group {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          margin-top: 15px;
          padding:30px;
        }

        .input-group input {
          padding: 8px;
          border: none;
          border-radius: 4px;
          background-color: #f1f1f1;
          width: 200px;
        }
        .display-group {
          display: flex;
          align-items: center;
          justify-content: flex-start;
        }
        .addimg:hover, .display-group button:hover, .savewrap button:hover{
          background:#7d1ac9;
          opacity:0.9;
        }

        button {
          margin-left: 35px;
          padding: 9px 22px;
          border: none;
          border-radius: 4px;
          font-size:16px;
          background-color: #a428db;
          color: #fff;
          cursor: pointer;
        }
      `}
      </style>
    </>
  );
};

export default ProfilePage;