import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const Notpage = () => {
  const [isHovered, setIsHovered] = useState(false);
  const [ishomeHovered, setIshomeHovered] = useState(false);

  const handleClick = () => {
    window.history.back();
  };

  const handleMouseEnter = () => {
    setIsHovered(true);
  };
  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  const handlehomeMouseEnter = () => {
    setIshomeHovered(true);
  };
  const handlehomeMouseLeave = () => {
    setIshomeHovered(false);
  };

  return (
    <>
      <div className='notfound-wrap'>
        <div className='Four-wrap'>
          <div className='Num' style={{color:'#7d1ac9'}}>4</div>
          <div className='Num' style={{color:'#a428db'}}>0</div>
          <div className='Num' style={{color:'#7d1ac9'}}>4</div>
        </div>
        <div style={{position:'absolute',top:'54%', fontWeight:'bold'}}>없는 페이지 입니다</div>
        <div className='change-page'>
          <div className='left' onClick={handleClick}>
            <p 
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}>GO BACK</p>
            {isHovered && <p style={{
              position:'absolute', 
              fontSize:'20px',
              top:'10%',
              marginLeft:'45%',
              borderRadius:'5px',
              opacity:'0.3',
              background:'#000'
              }}>뒤로 가기</p>}
          </div>
          <Link to='/'>
            <div className='right'>
            <p 
            onMouseEnter={handlehomeMouseEnter}
            onMouseLeave={handlehomeMouseLeave}>GO HOME</p>
            {ishomeHovered && <p style={{
              position:'absolute', 
              fontSize:'20px',
              top:'10%',
              marginLeft:'45%',
              borderRadius:'5px',
              opacity:'0.3',
              background:'#000'
              }}>메인 으로</p>}
            </div>
          </Link>
        </div>
      </div>
      <style jsx>{`
        .notfound-wrap{
          display:flex;
          align-items:center;
          justify-content:center;
        }
        .Four-wrap {
          display:flex;
          margin-top:230px;
        }
        .Num{
          font-size:100px;
          font-weight:bold;
          padding:30px;
          z-index:10;
        }
        .change-page{
          position:absolute;
          display:flex;
          justify-content:space-between;
          width:100%;
        }
        .change-page .left{
          position:absolute;
          text-align: center;
          width:50%;
          height:100vh;
          top:-220px;
          color:white;
          transition: background 0.3s ease;
          transition: color 0.3s ease;
        }
        
        .change-page .right{
          position:absolute;
          text-align: center;
          width:50%;
          left:50%;
          height:100vh;
          top:-220px;
          color:white;
          transition: background 0.5s ease;
        }
        .left:hover, .right:hover{
          background:gray;
          opacity:0.6;
          color:#fff;
        }
        .left p, .right p{
          margin-top:340px;
          font-size:30px;
          cursor:pointer;
          transition: font-size 0.38s ease;
        }
        .left p:hover, .right p:hover{
          margin-top:341px;
          font-size:38px;
        }
      `}</style>
    </>
  );
};

export default Notpage;