import React, { useState, useEffect, useRef } from "react";
import "../../css/search.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";


export const Search = () => {
  const [inputValue, setInputValue] = useState(null);
  const [limitvalue,setLimitvalue] = useState([]);
  const [tagvalue,setTagvalue] = useState([]);
  const [select, setSelect] = useState('block');
  const [tag, setTag] = useState('none');
  const id = sessionStorage.getItem("isLogin");
  const navigate = useNavigate();
  const [sentences] = useState([
    "HTML은 프로그래밍 언어가 아닙니다.",
    "long패키지보다 긴건? double코트 엌ㅋㅋ",
    "Hello World!",
    "세계 최초의 컴퓨터는 에니악입니다.",
    "java랑 javaScript는 전혀 다른 언어입니다.",
    "jsp는 javaScript가 아닙니다.",
  ]);
  const [currentSentence, setCurrentSentence] = useState("");
  const [isSearchClicked, setIsSearchClicked] = useState(false);
  const searchInputRef = useRef(null);
  const searchTableRef = useRef(null);

  useEffect(() => {
    axios.post("/api/searchHistory/show",{
      id
    }).then((res)=>{
      setTagvalue(res.data.tags);
      if(res.data.success){
        setLimitvalue(res.data.searchHistory);
      }
    })

  },[])

  useEffect(() => {
    const randomIndex = Math.floor(Math.random() * sentences.length);
    setCurrentSentence(sentences[randomIndex]);

    const handleOutsideClick = (e) => {
      if (
        searchInputRef.current &&
        !searchInputRef.current.contains(e.target) &&
        searchTableRef.current &&
        !searchTableRef.current.contains(e.target)
      ) {
        setIsSearchClicked(false);
      }
    };

    document.addEventListener("click", handleOutsideClick);
    return () => {
      document.removeEventListener("click", handleOutsideClick);
    };
  }, []);

  const handleSearchClick = () => {
    setIsSearchClicked(true);
  };
  const handleSearchEnter = (e) => {
    if(inputValue === null || inputValue === ' ' || inputValue === ''){
      return;
    }
    if (e.key === 'Enter') {
      e.preventDefault();
      let isTag = '';
      let IPvalue = inputValue;
      if(inputValue.slice(0,1) === '#'){
        isTag = 'tag';
        IPvalue = IPvalue.slice(1);
      }else{
        isTag = 'title';
      }
      navigate(`/Searchboardlist/${isTag}/${IPvalue}`);
    }
  };

  const clickList = (e) =>{
    e.preventDefault();

    let isTag = '';
      let IPvalue = e.target.id;
      if(IPvalue.slice(0,1) === '#'){
        isTag = 'tag';
        IPvalue = IPvalue.slice(1);
      }else{
        isTag = 'title';
      }
      navigate(`/Searchboardlist/${isTag}/${IPvalue}`);
  }

  const tagselect = (e) => {
    e.preventDefault();
    const IPvalue = e.target.id;
    navigate(`/Searchboardlist/tag/${IPvalue}`);
  }

  return (
    <div>
      <div className="search-container">
        <div className="input-container">
          <div className="text">
            <strong>{currentSentence}</strong>
          </div>
        </div>
        <div className="input-container1">
          <input type="search" className="search-input"
            placeholder="내 콘텐츠나 NULL 콘텐츠 검색"
            onClick={handleSearchClick}
            onKeyDown={handleSearchEnter}
            onChange={(e) => {
              setInputValue(e.target.value);
            }}
            ref={searchInputRef}
          />
          <div
            className={`search-table ${isSearchClicked ? "visible" : ""}`}
            ref={searchTableRef}
          >
            <div className="select-cont" >
              <button className={`select-button`} onClick={()=>{
                setSelect('block');
                setTag('none');
              }}>검색기록</button>
              <button className={`select-button`} onClick={()=>{
                setSelect('none');
                setTag('block');
              }}>태그 랭킹</button>
            </div>
              <div style={{display: select}}>
              {
                limitvalue.length === 0 ?
                <div style={{
                  textAlign: 'center'
                }}>
                  검색기록이 없습니다 #을 이용해 검색을 하면 태그를 검색 하실수 있습니다.
                </div>
                :
              limitvalue.map((limit, idx) => {
                return (
                  <div className="select" key={idx}>
                    <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 16 16" height="1.2em" width="1.2em" xmlns="http://www.w3.org/2000/svg">
                      <path d="M8.515 1.019A7 7 0 0 0 8 1V0a8 8 0 0 1 .589.022l-.074.997zm2.004.45a7.003 7.003 0 0 0-.985-.299l.219-.976c.383.086.76.2 1.126.342l-.36.933zm1.37.71a7.01 7.01 0 0 0-.439-.27l.493-.87a8.025 8.025 0 0 1 .979.654l-.615.789a6.996 6.996 0 0 0-.418-.302zm1.834 1.79a6.99 6.99 0 0 0-.653-.796l.724-.69c.27.285.52.59.747.91l-.818.576zm.744 1.352a7.08 7.08 0 0 0-.214-.468l.893-.45a7.976 7.976 0 0 1 .45 1.088l-.95.313a7.023 7.023 0 0 0-.179-.483zm.53 2.507a6.991 6.991 0 0 0-.1-1.025l.985-.17c.067.386.106.778.116 1.17l-1 .025zm-.131 1.538c.033-.17.06-.339.081-.51l.993.123a7.957 7.957 0 0 1-.23 1.155l-.964-.267c.046-.165.086-.332.12-.501zm-.952 2.379c.184-.29.346-.594.486-.908l.914.405c-.16.36-.345.706-.555 1.038l-.845-.535zm-.964 1.205c.122-.122.239-.248.35-.378l.758.653a8.073 8.073 0 0 1-.401.432l-.707-.707z"></path>
                      <path d="M8 1a7 7 0 1 0 4.95 11.95l.707.707A8.001 8.001 0 1 1 8 0v1z"></path>
                      <path d="M7.5 3a.5.5 0 0 1 .5.5v5.21l3.248 1.856a.5.5 0 0 1-.496.868l-3.5-2A.5.5 0 0 1 7 9V3.5a.5.5 0 0 1 .5-.5z"></path>
                    </svg>
                    <div className="select1" id={limit.contents} onClick={clickList}>{limit.contents}</div>
                  </div>
                );
              })
              }
              </div>
              <div style={{display: tag}}>
              {
              tagvalue.map((tag, idx) => {
                return (
                  <div className="select" key={idx}>
                    <div>
                      {idx+1}
                    </div>
                    <div className="select1" id={tag.name} onClick={tagselect}>#{tag.name}</div>
                  </div>
                );
              })
              }
              </div>
          </div>
        </div>
      </div>
    </div>
  );
};