import axios from "axios";
import "../css/Nav.css";
import React, { useState, useEffect, useRef } from "react";
import { useNavigate,useLocation  } from 'react-router-dom';

export const Nav = () => {
  const location = useLocation();
  const burgerRef = useRef(null);
  const profileRef = useRef(null);
  const [showBgLeft, setShowBgLeft] = useState(false);
  const [showBgRight, setShowBgRight] = useState(false);
  const [inputValue, setInputValue] = useState(null);
  const [profileNm, setProfileNm] = useState('');
  const [profileUm, setProfileUm] = useState('');
  const [profileEm, setProfileEm] = useState('');
  const [profileImg, setProfileImg] = useState('');
  const defaultImg = 'data:image/png;base64,';
  const navigate = useNavigate();
  const searchInputRef = useRef(null);
  const toggleBgLeft = () => {
    setShowBgLeft(true);
  };

  const toggleBgRight = () => {
    setShowBgRight(true);
  };
  const handleCopyClipBoard = async () => {
    try {
      await navigator.clipboard.writeText('http://54.180.213.51:8080/');
      alert("클립보드에 링크가 복사되었어요.");
    } catch (err) {
      console.log(err);
    }
  };
  
  useEffect(() => {
    axios({
      method: 'post',
      url: '/api/profile',
      data: {
        userid: sessionStorage.getItem("isLogin")
      }
    }).then((res) => {
      if (res.data.success) {
        setProfileNm(res.data.profileName);
        setProfileEm(res.data.profileEmail);
        setProfileImg(res.data.profileImg);
        setProfileUm(res.data.profileUm);
      }
    });
  }, [location]);

  useEffect(() => {
    let navSearchInput = document.querySelector('.nav-search-input');
    let scrollPosition = window.scrollY || window.pageYOffset;
    if(location.pathname !== '/'){
      navSearchInput.style.display = 'block';
    }else{
      if (scrollPosition > 150) {
        navSearchInput.style.display = 'block';
      } else {
        navSearchInput.style.display = 'none';
      }

    }
    const handleScroll = () => {
      let navSearchInput = document.querySelector('.nav-search-input');
      let scrollPosition = window.scrollY || window.pageYOffset;

      if(location.pathname === '/'){
        if (scrollPosition > 150) {
          navSearchInput.style.display = 'block';
        } else {
          navSearchInput.style.display = 'none';
        }
      }else{
        navSearchInput.style.display = 'block';
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [location]);

  const logout = () => {
    axios({
      method: 'post',
      url: '/api/logout'
    })
      .then((res) => {
        if (res.data.success) {
          sessionStorage.setItem("isLogin", "");
          window.location.reload();
        } else {
          alert(res.data.message);
        }
      })
      .catch(err => console.log(err.message));
  };

  const belogin = () => {
    if (sessionStorage.getItem("isLogin")) {
      navigate('/boardwrite');
    } else {
      navigate('/login');
      alert("로그인 후 이용가능합니다.");
    }
  };

  const handleSearchEnter = (e) => {
    if(inputValue === null || inputValue === ' ' || inputValue === ''){
      return;
    }
    if (e.key === 'Enter') {
      e.preventDefault();
      let isTag = '';
      let IPvalue = inputValue;
      if(inputValue.slice(0,1) === '#'){
        isTag = 'tag';
        IPvalue = IPvalue.slice(1);
      }else{
        isTag = 'title';
      }
      navigate(`/Searchboardlist/${isTag}/${IPvalue}`);
    }
  };
  const setting = () =>{
    const isLogin = sessionStorage.getItem("isLogin");
    if(isLogin){
      navigate('/profilesettig');
    }else{
      alert('로그인 먼저 해주세요.');
      navigate('/login');
    }
  }
  const my = () =>{
    navigate('/');
    window.scrollTo(0,800)
  }

  useEffect(()=>{
    const handleOutsideClick = (event) => {
      if (burgerRef.current && !burgerRef.current.contains(event.target)) {
        setShowBgLeft(false);
      }
    };
  
    document.addEventListener('mousedown', handleOutsideClick);
  
    return () => {
      document.removeEventListener('mousedown', handleOutsideClick);
    };
  },[burgerRef])


  useEffect(()=>{
    const handleOutsideClick = (event) => {
      if (profileRef.current && !profileRef.current.contains(event.target)) {
        setShowBgRight(false);
      }
    };
  
    document.addEventListener('mousedown', handleOutsideClick);
  
    return () => {
      document.removeEventListener('mousedown', handleOutsideClick);
    };
  },[profileRef])
  return (
    <div>
      <nav className="navB">
        <div className="burgerBar" ref={burgerRef} onClick={toggleBgLeft}></div>
        <div className="NULL" onClick={()=>{
          navigate("/");
        }}>CodeSphere</div>
          <input type="search" className = "nav-search-input1" 
            onKeyDown={handleSearchEnter}
            ref={searchInputRef}
            value={inputValue}
            onChange={(e) => {
              setInputValue(e.target.value);
            }}
            placeholder="내 콘텐츠나 NULL 콘텐츠 검색"></input>
        <div className="prp-container">
        <input type="search" className = "nav-search-input" 
            onKeyDown={handleSearchEnter}
            ref={searchInputRef}
            value={inputValue}
            onChange={(e) => {
              setInputValue(e.target.value);
            }}
            placeholder="내 콘텐츠나 NULL 콘텐츠 검색"></input>

          <div className="Setting" onClick={setting}></div>

          <div className="make" onClick={belogin}>게시물 만들기</div>
          <div className="prp" onClick={toggleBgRight} style={{
            backgroundImage: `url(${ profileImg || defaultImg})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
          }}></div>
        </div>
      </nav>
      <div ref={burgerRef} className={showBgLeft ? "bg-container" : "bg-container-none"}>
        <div className="burger-prp-container1">
          <div className="burger-propile" style={{
            backgroundImage: `url(${ profileImg || defaultImg})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
          }}></div> 
          <div className="burger-nickName"><strong>{profileNm}{`(${profileUm})`}</strong></div>
          {/* <div className="burger-phoneNum"><svg width="15" height="15" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M14.899 11.045C16.162 10.132 17 8.657 17 7c0-2.736-2.264-5-5-5S7 4.264 7 7c0 1.684.862 3.184 2.159 4.093C6.185 12.353 4.074 15.458 4 19.06c0 .418.193.732.482.941 2.313 1.255 4.819 1.986 7.518 1.986s5.301-.732 7.518-1.986c.289-.209.482-.523.482-.941 0-3.651-2.094-6.788-5.101-8.015ZM8.5 7c0-1.897 1.603-3.5 3.5-3.5s3.5 1.603 3.5 3.5c0 1.859-1.541 3.425-3.387 3.488l-.017-.001c-.048 0-.095.007-.143.008C10.077 10.468 8.5 8.881 8.5 7ZM12 20.487c-2.198 0-4.38-.552-6.492-1.64.192-3.752 3.026-6.769 6.444-6.852.017 0 .032.005.048.005.039 0 .075-.011.114-.012 3.459.011 6.283 3.066 6.383 6.862A13.784 13.784 0 0 1 12 20.487Z" fill="currentColor"></path></svg>  • 2</div> */}
        </div>
        <div onClick={()=>{navigate('/')}} className="bg-set-container">
        <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 1024 1024" height="1.2em" width="1.2em" xmlns="http://www.w3.org/2000/svg"><path d="M946.5 505L560.1 118.8l-25.9-25.9a31.5 31.5 0 0 0-44.4 0L77.5 505a63.9 63.9 0 0 0-18.8 46c.4 35.2 29.7 63.3 64.9 63.3h42.5V940h691.8V614.3h43.4c17.1 0 33.2-6.7 45.3-18.8a63.6 63.6 0 0 0 18.7-45.3c0-17-6.7-33.1-18.8-45.2zM568 868H456V664h112v204zm217.9-325.7V868H632V640c0-22.1-17.9-40-40-40H432c-22.1 0-40 17.9-40 40v228H238.1V542.3h-96l370-369.7 23.1 23.1L882 542.3h-96.1z"></path></svg>
            <div className="prp-setting1"><div className="prp-setting-text"><strong>홈</strong></div></div>
        </div>
        <div className="bg-set-container2" onClick={()=>{navigate('/projectlist')}}>
        <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 24 24" height="1.2em" width="1.2em" xmlns="http://www.w3.org/2000/svg"><path fill="none" stroke="#000" strokeWidth="2" d="M9,15 L9,23 L1,23 L1,15 L9,15 Z M23,15 L23,23 L15,23 L15,15 L23,15 Z M9,1 L9,9 L1,9 L1,1 L9,1 Z M23,1 L23,9 L15,9 L15,1 L23,1 Z"></path></svg>
            <div className="prp-setting1" ><div className="prp-setting-text"><strong>프로젝트</strong></div></div>
        </div>
        <div className="bg-set-container3" onClick={()=>{navigate('/chatlist')}}>
        <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 512 512" height="1.2em" width="1.2em" xmlns="http://www.w3.org/2000/svg"><path d="M488 348.78h-70.24l-15.1 87.44-48.78-87.44H169v-50h190v-157h129zm-145-273v207H158.13l-48.79 87.47-15.11-87.47H24v-207zM136.724 215.324c0-10.139-12.257-15.214-19.425-8.046-7.168 7.168-2.093 19.426 8.046 19.426 6.285 0 11.38-5.095 11.38-11.38zm60.945 0c-.068-10.12-12.32-15.122-19.452-7.943-7.131 7.18-2.047 19.399 8.073 19.399 6.314 0 11.422-5.141 11.38-11.456zm60.945 0c0-10.139-12.257-15.214-19.425-8.046-7.169 7.168-2.093 19.426 8.046 19.426 6.284 0 11.38-5.095 11.38-11.38z"></path></svg>
            <div className="prp-setting1" ><div className="prp-setting-text"><strong>잡담</strong></div></div>
        </div>
          {
          sessionStorage.getItem("isLogin") ?
            <>
            <div className="bg-set-container4" onClick={my}>
            <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 24 24" height="1.2em" width="1.2em" xmlns="http://www.w3.org/2000/svg"><path fill="none" d="M0 0h24v24H0z"></path><path d="M5 5h2v3h10V5h2v5h2V5c0-1.1-.9-2-2-2h-4.18C14.4 1.84 13.3 1 12 1s-2.4.84-2.82 2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h5v-2H5V5zm7-2c.55 0 1 .45 1 1s-.45 1-1 1-1-.45-1-1 .45-1 1-1z"></path><path d="M20.3 18.9c.4-.7.7-1.5.7-2.4 0-2.5-2-4.5-4.5-4.5S12 14 12 16.5s2 4.5 4.5 4.5c.9 0 1.7-.3 2.4-.7l2.7 2.7 1.4-1.4-2.7-2.7zm-3.8.1c-1.4 0-2.5-1.1-2.5-2.5s1.1-2.5 2.5-2.5 2.5 1.1 2.5 2.5-1.1 2.5-2.5 2.5z"></path></svg>
                <div className="prp-setting1"><div className="prp-setting-text"><strong>내 콘텐츠</strong></div></div>
            </div>
            <div className="bg-set-container5" onClick={logout}>
            <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 24 24" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M16 13v-2H7V8l-5 4 5 4v-3z"></path><path d="M20 3h-9c-1.103 0-2 .897-2 2v4h2V5h9v14h-9v-4H9v4c0 1.103.897 2 2 2h9c1.103 0 2-.897 2-2V5c0-1.103-.897-2-2-2z"></path></svg>
                <div className="prp-setting1" ><div className="prp-setting-text"><strong>로그아웃</strong></div></div>
            </div>
            </>
          :
          <>
            <div className="bg-set-container4" onClick={()=>{navigate('/login')}}>
            <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 24 24" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="m13 16 5-4-5-4v3H4v2h9z"></path><path d="M20 3h-9c-1.103 0-2 .897-2 2v4h2V5h9v14h-9v-4H9v4c0 1.103.897 2 2 2h9c1.103 0 2-.897 2-2V5c0-1.103-.897-2-2-2z"></path></svg>
                <div className="prp-setting1" ><div className="prp-setting-text"><strong>로그인</strong></div></div>
            </div>
            <div className="bg-set-container5" onClick={()=>{navigate('/signUp')}}>
            <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 16 16" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"></path><path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"></path></svg>
                <div className="prp-setting1" ><div className="prp-setting-text"><strong>회원가입</strong></div></div>
            </div>
          </>
          }
        <div className="friends">
          <label
            style={{
              width: '100%',
              height: '100%'
              
            }}
            className="friends2"
            onClick={handleCopyClipBoard}>
            
            친구 초대
          </label>
        </div>
      </div>
      <div className="bg-container2" ref={profileRef} style={{ display: showBgRight ? "block" : "none" }}>
        <div className="prp-container1">
          <div className="propile" style={{
            backgroundImage: `url(${ profileImg || defaultImg})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
          }}></div>
          <div className="nickName"><strong>{profileNm ? profileNm : "로그인 해주세요"}</strong></div>
          <div className="phoneNum">{profileEm}</div>
        </div>
        {
          sessionStorage.getItem("isLogin") ?
          <>
            <div className="set-container">
              <ul>
                <div className="prp-setting" onClick={()=>{navigate('/profilesettig')}}><div className="prp-setting-text"><strong>계정 설정</strong></div></div>
              </ul>
            </div>
          </>
          :
          <>
          </>
        }
        <div className="set-container2">
          <ul>
            <div className="prp-setting" onClick={()=>{navigate('/inquiry')}}><div className="prp-setting-text"><strong>고객센터</strong></div></div>
          </ul>
        </div>
            <div className="set-container4">
              <ul>
                <div className="prp-setting"><div className="prp-setting-text">
                  <strong onClick={handleCopyClipBoard}>
                    친구 초대</strong></div></div>
              </ul>
            </div>
        {
          sessionStorage.getItem("isLogin") ?
          <>
            <div className="set-container5" onClick={logout}>
              <ul>
                <div className="prp-setting" ><div className="prp-setting-text"><strong>로그아웃</strong></div></div>
              </ul>
            </div>
          </>
          :
          <>
            <div className="set-container4" onClick={()=>{navigate('/login')}}>
              <ul>
                <div className="prp-setting" ><div className="prp-setting-text"><strong >로그인</strong></div></div>
              </ul>
            </div>
            <div className="set-container5" onClick={()=>{navigate('/signUp')}}>
              <ul>
                <div className="prp-setting" ><div className="prp-setting-text"><strong>회원가입</strong></div></div>
              </ul>
            </div>
          </>
        }
      </div>
    </div>
  );
};  
export default Nav;
