import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useParams } from "react-router-dom";
const CommentComponent = () => {
  const { id } = useParams();
  const [comments, setComments] = useState([]);
  const [newComment, setNewComment] = useState('');
  const [editCommentIdx, setEditCommentIdx] = useState(-1);
  const [editedComment, setEditedComment] = useState('');
  const [showOptionsMenu, setShowOptionsMenu] = useState(false);
  const [selectedCommentIdx, setSelectedCommentIdx] = useState(-1);
  const [displayedComments, setDisplayedComments] = useState(2);
  const [showAllComments, setShowAllComments] = useState(true);
  const [showComments, setShowComments] = useState(false);
  const user_id = parseInt(sessionStorage.getItem("isLogin"));
  const [comment_wiret_user, setComment_wiret_user] = useState('');
  const handleCommentChange = (e) => {
    setNewComment(e.target.value);
  };

  const handleCommentSubmit = (e) => {;
    e.preventDefault();
    if (newComment.trim() !== '') {
      setComments([...comments, newComment]);
      axios.post("/api/comment/write",{
        user_id: user_id,
        content: newComment,
        post_id: id
      }).then((res)=>{
        if(res.data.success){
          take();
        }
      })
      setNewComment('');
    }
  };

  const handleCommentEdit = (idx) => {
    setEditCommentIdx(idx);
    setEditedComment(comments[idx].content);
  };

  const handleCommentUpdate = (e,idx) => {
    e.preventDefault();
    if (editedComment.trim() !== '') {
      const comment_id = comments[idx].id;

      axios.post("/api/comment/update",{
        content: editedComment,
        id: comment_id
      }).then((res)=>{
        if(res.data.success){
          take();
        }
      })

      setEditCommentIdx(-1);
      setEditedComment('');
    }
  };

  const handleCommentDelete = (idx) => {
    const comment_id = comments[idx].id;
    axios.post("/api/comment/delete",{
      id:comment_id
    }).then((res)=>{
      if(res.data.success){
        take();
      }
    })
  };

  const handleOptionsMenu = (idx) => {
    setSelectedCommentIdx(idx);
    setShowOptionsMenu(true);
  };

  /**prevCount + n ,n은 댓글 더보기 클릭시 댓글 추가로 보여 주는 수 (displayedComments 의 useState(n) 같이 변경) */
  const handleLoadMoreComments = () => {
    setDisplayedComments((prevCount) => prevCount + 2);
  };

  const handleToggleShowComments = () => {
    setShowAllComments((prevState) => !prevState);
    setDisplayedComments((prevState) => (prevState === 2 ? 1 : comments.length));
  };

  useEffect(() => {
    const handleDocumentClick = (event) => {
      const commentOptionMenu = event.target.closest(".comment-option-menu");
      const commentOptions = event.target.closest(".comment-options");

      if (!commentOptionMenu && !commentOptions) setShowOptionsMenu(false);
    };

    document.addEventListener("click", handleDocumentClick);
    return () => {
      document.removeEventListener("click", handleDocumentClick);
    };
  }, []);

  useEffect(() => {
    setShowComments(comments.length > 0);
  }, [comments]);
const take = () => {
  axios.post("/api/comment/take",{
    id: id
  }).then((res) => {
    if(res.data.success){
      setComments(res.data.comments);
      setComment_wiret_user(res.data.writeUsername)
    }
  })
}

  useEffect(()=>{
    take();
  },[])
  const report = (id, isPost) => {
    const userId = sessionStorage.getItem('isLogin');
    if (isPost !== 1) { // 수정된 부분
      isPost = false; // 수정된 부분
    }
    axios.post('/api/reports', {
      user_id: userId,
      id,
      isPost
    }).then((res) => {
      if (res.data.success) {
        alert('신고 되었습니다');
      }
    });
  };
  
  const times = (time) =>{

    const now = new Date();
  
  
    const givenTime = new Date(time);
  
  
    const timeDiff = now - givenTime;
  
  
    const msPerSecond = 1000;
    const msPerMinute = msPerSecond * 60;
    const msPerHour = msPerMinute * 60;
    const msPerDay = msPerHour * 24;
    const msPerMonth = msPerDay * 30; 
  
    let result;
  
    if (timeDiff < msPerMinute) {
    // 1분 미만인 경우
    result = '방금 전';
    } else if (timeDiff < msPerHour) {
    // 1시간 미만인 경우
    const minutes = Math.floor(timeDiff / msPerMinute);
    result = `${minutes}분 전`;
    } else if (timeDiff < msPerDay) {
    // 1일 미만인 경우
    const hours = Math.floor(timeDiff / msPerHour);
    result = `${hours}시간 전`;
    } else if (timeDiff < msPerMonth) {
    // 1달 미만인 경우
    const days = Math.floor(timeDiff / msPerDay);
    result = `${days}일 전`;
    } else {
    // 1달 이상인 경우
    const months = Math.floor(timeDiff / msPerMonth);
    result = `${months}개월 전`;
    }
    return result;
  }
  
  return (
    <div className="Comment-component">
      <form className="comment-upload" onSubmit={handleCommentSubmit}>
        <input  type="text" value={newComment} className="comment-input"
          placeholder="댓글을 입력하세요..."
          onChange={handleCommentChange}
        />
        <button className="input-btn" type="submit">추가하기</button>
      </form>

      {showComments && (
        <ul className="comment-box">
          {comments.slice(0, showAllComments ? displayedComments : 1).map((comment, idx) => (
            <li key={idx}>
              {editCommentIdx === idx ? (
                <form className="comment-upload" onSubmit={(e)=> handleCommentUpdate(e,idx)}>
                  <input className="comment-input" type="text" value={editedComment}
                    onChange={(e) => setEditedComment(e.target.value)}
                  />
                  <button className="input-btn" type="submit">저장</button>
                </form>
              ) : (
                <div className="comment-container">
                  <div className="comment-head">
                    <h6 className="comment-name by-author">{comment.username}</h6>
                    <span>{times(comment.updated_at)}</span>
                  </div>
                  <div className="comment-board">
                    <span className="comment-content">{comment.content}</span>
                    <div className="comment-options">
                      <button className="comment-option-btn" style={{ fontSize: '20px' }}
                        onClick={() => handleOptionsMenu(idx)}
                      >
                        ...
                      </button>
                      {showOptionsMenu && selectedCommentIdx === idx && (
                        <div className="comment-option-menu">
                          {user_id === comment.user_id ?
                          (
                          <div>
                            <button className="input-btn rmt" onClick={() => handleCommentEdit(idx)}>
                              수정
                            </button>
                            <button className="input-btn rmt" onClick={() => handleCommentDelete(idx)}>
                              삭제
                            </button>
                          </div>
                          )
                          :
                          (
                            <button className="input-btn rmt"
                            onClick={()=>{
                              report(id,0)
                            }} 
                            >
                              신고
                            </button>
                          )}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )}
            </li>
          ))}
        </ul>
      )}
      {comments.length > 0 && (
        <button className="toggle-comments" onClick={handleToggleShowComments}>
          {showAllComments ? `댓글 닫기` : `댓글 보기 (${comments.length})`}
        </button>
      )}
      {comments.length > displayedComments && showAllComments && (
        <button className="load-more-comments" onClick={handleLoadMoreComments}>
          더보기({comments.length})
        </button>
      )}
      

      <style jsx>{`
        * {
          margin: 0;
          padding: 0;
          box-sizing: border-box;
          list-style-type: none;
        }
        .Comment-component {
          -webkit-box-sizing: border-box;
          -moz-box-sizing: border-box;
          width: 90%;
        }
        .comment-container {
          padding: 6px;
          width: 100%;
        }
        .comment-container:hover { background: #eeec;}
        .comment-input { flex: 1;}
        .comment-upload {
          display: flex;
          padding: 10px;
          border-top: var(--border);
          background: #eee;
        }
        .comment-upload *,
        .comment-box {
          padding: 8px;
          border: none;
          border-radius: 3px;
          font-size: 1em;
        }
        .comment-box .comment-head {
          background: #fcfcfc;
          padding: 1% 3%;
          border-bottom: 1px solid #e5e5e5;
          overflow: hidden;
          -webkit-border-radius: 4px 4px 0 0;
          -moz-border-radius: 4px 4px 0 0;
          border-radius: 4px 4px 0 0;
        }
        .comment-name {
          color: #283035;
          font-size: 14px;
          font-weight: 700; 
          float: left;
          margin-right: 10px;
        }
        .comment-box .comment-head span {
          float: left;
          color: #999;
          font-size: 13px;
          position: relative;
          top: 1px;
        }
        .comment-box .comment-content {
          background: #fff;
          padding: 4px;
          font-size: 15px;
          color: #595959;
          -webkit-border-radius: 0 0 4px 4px;
          -moz-border-radius: 0 0 4px 4px;
          border-radius: 0 0 4px 4px;
        }
        .comment-box .comment-name.by-author:after,
        .input-btn {
          content: '${comment_wiret_user}';
          background: #a428db;
          color: #fff;
          font-size: 12px;
          padding: 3px 5px;
          font-weight: 700;
          margin-left: 10px;
          -webkit-border-radius: 3px;
          -moz-border-radius: 3px;
          border-radius: 3px;
        }
        .input-btn { background: #a428db; }
        .input-btn:hover { background: #7d1ac9; }
        .rmt { color: #000; border: 1px solid #eeec; }
        .comment-board {
          display: flex;
          justify-content: space-between;
        }
        .comment-options {
          position: relative;
          display: inline-block;
        }
        .comment-option-btn {
          background: none;
          border: none;
          cursor: pointer;
        }
        .comment-option-menu {
          position: absolute;
          height: 30px;
          width: 110px;
          top: 0;
          margin-left: -100px;
          z-index: 10;
          display: block;
          border-radius: 4px;
          padding: 5px;
        }
        .comment-option-menu button {
          background: none;
          cursor: pointer;
          padding: 5px;
        }
        .load-more-comments, .toggle-comments {
          background: none;
          border: none;
          cursor: pointer;
          font-size: 14px;
          margin-top: 10px;
          margin-bottom:10px;
          color: #595959;
        }
        .load-more-comments:hover,
        .toggle-comments:hover,.comment-option-btn:hover{
          text-decoration: underline;
        }
        .load-more-comments{
          margin-left:83.5%;
        }
      `}</style>
    </div>
  );
};

export default CommentComponent;
