import React from 'react';
import { useNavigate } from 'react-router-dom';
const Hashtag = ({ tag }) => {
  const navigate = useNavigate();
  return (
    <>
      <span className="hashtag" onClick={()=>{navigate(`/Searchboardlist/tag/${tag.name}`)}}>{tag.name}</span>
      <style jsx>{`
        .hashtag{
          font-size:16px;
          font-weight:400;
          margin:5px;
          padding:2px 10px 4px 13px;
          background:#f8f9fa;
          color:#12b886;
          border-radius:20px;
          cursor: pointer;
        }
        .hashtag:hover{
          color:#51cba6;
          background:#fff;
        }
        `}</style>
    </>
  );
};

export default Hashtag;