import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import '../../css/CSSadmin/ReportPage.css'; 
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import NewNav from './adminNav2';

const ReportListPage = () => {
  const navigate = useNavigate();
  const [reports, setReports] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const id = sessionStorage.getItem('isLogin');

  useEffect(() => {
    fetchReports();
  }, []);

  const fetchReports = () => {
    axios.post('/api/admin/reports', { id: id })
      .then((res) => {
        if (res.data.success) {
          setReports(res.data.reports);
          const totalPagesCount = Math.ceil(res.data.reports.length / 10);
          setTotalPages(totalPagesCount);
        } else {
          alert('비정상적인 접근입니다');
          navigate('/');
        }
      })
      .catch((error) => {
        console.error('신고 목록을 불러오는 중 에러 발생:', error);
      });
  };

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const getPageReports = () => {
    const startIndex = (currentPage - 1) * 10;
    const endIndex = startIndex + 10;
    return reports.slice(startIndex, endIndex);
  };

  const reportType = (post_id, comment_id) => {
    if (post_id !== undefined) {
      return "게시물";
    } else {
      return "댓글";
    }
  };

  return (
    <div className="report-list-container">
      <NewNav/>
      <h1>신고 목록</h1>
      <div className="report-table-container">
        <table className="report-table">
          <thead>
            <tr>
              <th>신고 번호</th>
              <th>신고 유형</th>
              <th>신고자 아이디</th>
            </tr>
          </thead>
          <tbody>
            {getPageReports().map((report) => (
              <tr key={report.id}>
                <td>
                  <Link to={`/admin/ReportDetailPage/${report.id}`}>
                    <span className="report-link">{report.id}</span>
                  </Link>
                </td>
                <td>{reportType(report.post_id, report.comment_id)}</td>
                <td>{report.user_id}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="pagination">
        {Array.from({ length: totalPages }, (_, index) => (
          <button
            key={index + 1}
            onClick={() => handlePageChange(index + 1)}
            className={currentPage === index + 1 ? 'active' : ''}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
};

export default ReportListPage;
