
import { Search } from"./mains/search";
import { Rcm } from "./mains/rcm";
import { My } from "./mains/my";

import { useEffect, useState } from "react";

const Main = () => {
  const [isLogin, setIsLogin] = useState('');

  useEffect(() => setIsLogin(sessionStorage.getItem("isLogin")), []);
  
  return(
    <div style={{marginTop: '100px'}}>
    <Search></Search>
    <Rcm></Rcm>
    {
       isLogin ?
       <My></My>
       :
       ""
    }
    </div>
  )
}


export default Main;