import React from 'react';
import Hashtag from './HashTag';

const Post = ({ hashtags }) => {
  return (
    <div className="post">
      {hashtags.map((tag, index) => (
        <Hashtag key={index} tag={tag} />
      ))}
      <style jsx>{`
        
        .post {
          display: flex;
          flex-wrap: wrap;
          width: 90%;
          height: 100%;
        }
      `}</style>
    </div>
  );
};

export default Post;