import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import NewNav from "./adminNav2";
import axios from 'axios';

const MemberTable = () => {
  const navigate = useNavigate();
  const [members, setMembers] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const id = sessionStorage.getItem('isLogin');
  
  useEffect(() => {
    fetchMembers();
  }, []);

  const fetchMembers = () => {
    axios.post('/api/admin/members', { id: id })
      .then((res) => {
        if (res.data.success) {
          setMembers(res.data.members);
          const totalPagesCount = Math.ceil(res.data.members.length / 10);
          setTotalPages(totalPagesCount);
        } else {
          alert('비정상적인 접근입니다');
          navigate('/');
        }
      })
      .catch((error) => {
        console.error('멤버 목록을 불러오는 중 에러 발생:', error);
      });
  };

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  // 현재 페이지에 해당하는 멤버 목록 추출
  const getPageMembers = () => {
    const startIndex = (currentPage - 1) * 10;
    const endIndex = startIndex + 10;
    return members.slice(startIndex, endIndex);
  };

  return (
    <div className="member-table-container">
      <NewNav />
      <h1>사용자 목록</h1>
      <table className="member-table">
        <thead>
          <tr>
            <th>이름</th>
            <th>ID</th>
            <th>이메일</th>
          </tr>
        </thead>
        <tbody>
          {getPageMembers().map((member) => (
            <tr key={member.name}>
              <td>{member.name}</td>
              <td>{member.username}</td>
              <td>{member.email}</td>
            </tr>
          ))}
        </tbody>
      </table>
      
      {/* 페이징 */}
      <div className="pagination">
        {Array.from({ length: totalPages }, (_, index) => (
          <button
            key={index + 1}
            onClick={() => handlePageChange(index + 1)}
            className={currentPage === index + 1 ? 'active' : ''}
          >
            {index + 1}
          </button>
        ))}
      </div>
      
      <style jsx>{`
        .member-table-container {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-top: 100px;
        }
        
        .member-table {
          margin-top: 20px;
          border-collapse: collapse;
          width: 100%;
        }
        
        .member-table th,
        .member-table td {
          padding: 10px;
          border: 1px solid #ccc;
        }
        
        .member-table th {
          background-color: #f2f2f2;
          font-weight: bold;
        }
        
        .pagination {
          margin-top: 20px;
        }
        
        .pagination button {
          margin: 0 5px;
          padding: 5px 10px;
          border: 1px solid #ccc;
          background-color: #fff;
          cursor: pointer;
        }
        
        .pagination button.active {
          background-color: #f2f2f2;
        }
      `}</style>
    </div>
  );
};

export default MemberTable;
