import React,{useState} from 'react'
import { Link,useNavigate } from 'react-router-dom';
import axios from "axios";

const Signup = () => {
  const navigate = useNavigate();
  const [isVerificationEnabled, setIsVerificationEnabled] = useState(true);
  const [isVerificationCodeVisible, setIsVerificationCodeVisible] = useState(false);
  const [email, setEmail] = useState('');
  const [emailers,setEmailers] = useState('');
  const [emailchker, setEmailchker] = useState(false);
  const [verificationCode, setVerificationCode] = useState('');
  const [isTooltipVisible, setIsTooltipVisible] = useState(false);
  const emailer = () =>{
    const email = document.querySelector("#email");

    axios({
      method: 'post',
      url: "/api/email",
      data: {
        email: email.value
      } 
    }).then((res)=>{
      if(res.data.success){
        setEmailers(res.data.email);
        console.log(res.data.isEmail);
      }else{
        alert('이미 존재하는 이메일 입니다');
        setIsVerificationCodeVisible(false);
        setEmail('');
      }
    })
  }

  const emailchk = () =>{
    const emailchkInput = document.querySelector("#chk-text");
    if(emailers === emailchkInput.value){
      alert("인증 완료 되었습니다.");
      setEmailchker(true);
      return;
    }
    alert("인증번호가 다릅니다.");
    return;
    
  }

  const handleVerificationClick = () => {
    if (email.indexOf("@") !== -1) {
      emailer();
      setIsVerificationCodeVisible(true);
    } else {
      alert("이메일을 다시 써주세요");
    }
  };
  
  


  
  

  const [isChecked, setIsChecked] = useState(false);

  const handleCheckboxChange = (event) => {
    setIsChecked(event.target.checked);
    setIsTooltipVisible(false);
  };

  const handleCheckboxHoverOut = () => {
    setIsTooltipVisible(false);
  };
  const showConfirmationDialog = () => {
    if (isChecked) {
      alert('개인 정보 동의');
    } else {
      setIsTooltipVisible(true);
    }
  };

  const registers = () => {
    const id = document.querySelector("#userID");
    const pw = document.querySelector("#password");
    const REpw = document.querySelector("#repassword");
    const name = document.querySelector("#userName");
    const email = document.querySelector("#email");
    if(!name.value){return alert("이름을 입력해주세요.");}
    else if(!id.value){return alert("아이디를 입력해 주세요.");}
    else if(!pw.value){return alert("패스워드를 입력해주세요.");}
    else if(!REpw.value){return alert("확인 패스워드를 입력해주세요.");}
    else if(!email.value){return alert("이메일을 입력해주세요.");}
    else if(!emailchker){return alert("이메일 인증을 해주세요");}
    else if(pw.value !== REpw.value){return alert('비밀번호가 다릅니다');}

    axios({
      method: 'post',
      url: '/api/register', 
      data: {
        id: id.value,
        pw: pw.value,
        name: name.value,
        email: email.value
      }
    })
    .then((res) => {
      if(res.data.success) navigate('/login');
      else alert(res.data.message);
    })
    .catch(err => console.log(err.message));
    
  }


  return (
    <div className="page">
      <div className="container-signup">
        <div className="signup-box">
          <div>
          <h1 id="sg">SIGN UP</h1>
            <div className="signup">
              <input id="userName" type="text" placeholder="Name"/>
              <input id="userID" type="text" placeholder="ID"/>
              <input id="password" type="password" placeholder="PW"/>
              <input id="repassword" type="password" placeholder="Repassword"/>
              <input id="email" type="email" placeholder="Email" value={email}
                onChange={(e) => setEmail(e.target.value)}/>
              <div className="paer">
              {isVerificationCodeVisible ? (
                <>
                  <input id="chk-text" type="text" placeholder="인증번호" value={verificationCode}
                    onChange={(e) => setVerificationCode(e.target.value)}/>
                  <input onClick={emailchk} style={{ width:'150px', height:'40px', marginLeft:'30px', borderRadius:'10px'}} 
                  className='buttons' type="button" value="인증 확인" />
                </>
              ) : (
                <input
                  style={{ width:"200px", marginLeft:"110px"}}
                  className='buttons'type="button" value="인증하기"
                  onClick={handleVerificationClick}
                  disabled={!isVerificationEnabled} />
              )}
            </div>
            <button style={{marginLeft:'60px'}}
              className='buttons' onClick={registers}>SIGNUP</button>
            <p style={{marginTop:'10px'}}
            ><Link to='/login'  style={{ 
                textDecoration: 'none',
                color:"#4c80f1",
                fontSize:'13px',
                marginLeft:'40%',
                cursor:'pointer'
            }}>회원 이신가요?</Link></p>
          </div>
        </div>
      </div>
    </div>
    <style jsx>{`
  @import url("https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");

  * {
    padding: 0;
    margin: 0;
    border: 0;
    outline: 0;
  }
  .container-signup {
    margin-top:100px;
    background:#fff;
    height: 85vh;
    display: flex;
    flex-flow: column;
  }
  .signup-box {
    display: flex;
    justify-content: center;
    top: 7%;
    position: sticky;
  }
  .signup {
    width: 410px;
    padding: 20px;
  }
  #sg {
    margin:10px 10px 10px 15px;
    text-align: center;
    font-size: 28px;
    padding: 19px;
  }
  .signup input {
    width: 100%;
    padding: 10px;
    margin-left:-10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    font-size: 14px;
  }
  .paer{
    display:flex;
    align-items:center;
  }
  .round {
    top:-15px;
    position: relative;
  }
  .round label {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 50%;
    cursor: pointer;
    height: 28px;
    left: 0;
    position: absolute;
    top: 8px;
    width: 28px;
  }
  .round label:after {
    border: 2px solid #fff;
    border-top: none;
    border-right: none;
    content: "";
    height: 6px;
    left: 7px;
    opacity: 0;
    position: absolute;
    top: 8px;
    transform: rotate(-45deg);
    width: 12px;
  }
  .round input[type="checkbox"] {
    visibility: hidden;
  }

  .round input[type="checkbox"]:checked + label {
    background-color: #66bb6a;
    border-color: #66bb6a;
  }

  .round input[type="checkbox"]:checked + label:after {
    opacity: 1;
  }
  #chk-text{
    width:140px;
    height:20px;
    margin-left:40px;
  }
  .signup input[type="text"],
  input[type="password"],input[type="email"] {
    margin-bottom: 1em;
    background: #fff;
    border:1px solid gray;
    border-radius: 10px;
    color: #000;
    padding-right: 30px;
  }

  .signup input[type="text"]:focus,
  input[type="password"]:focus,input[type="email"]:focus {
    border-color: #4c80f1;
  }

  .buttons {
    display: inline-block;
    padding: 0;
    width: 300px;
    height: 44px;
    background:#a428db;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    color: #f9f9f9;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    white-space: nowrap;
  }
  .buttons:hover {
    background-color: #7d1ac9;
  }
  .buttons:active {
    background-color: #7d1ac9;
    animation: particlesEffect 0.2s;
  }
  @keyframes particlesEffect {
    0%{transform: scale(1);}
    50%{transform: scale(1.03);}
    100%{transform: scale(1);}
  }
`}</style>

    </div>
  );
}

export default Signup;