import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const Findpw = () => {
  const navigate = useNavigate();
  const [isVerificationEnabled, setIsVerificationEnabled] = useState(true);
  const [isVerificationCodeVisible, setIsVerificationCodeVisible] = useState(false);
  const [number, setNumber] = useState('')
  const [email, setEmail] = useState('');
  const emailInputRef = useRef(null);
  const [verificationCode, setVerificationCode] = useState('');
  const [check, setCheck] = useState(false);

  const isEmailValid = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleVerificationClick = (e) => {
    e.preventDefault();
      if (!email) {
      alert('이메일 주소를 입력해주세요.');
      emailInputRef.current.focus();
      return;
    } else if (!isEmailValid(email)) {
      alert('유효한 이메일 주소를 입력해주세요.');
      setEmail('');
      emailInputRef.current.focus();
      return;
    }
    axios.post('/api/email',{
      email
    }).then((res)=>{
      if(res.data.success){
        setNumber(res.data.email);
      }
    })

    setIsVerificationEnabled(false);
    setIsVerificationCodeVisible(true);
    setTimeout(() => {
      setIsVerificationCodeVisible(true);
    }, 1000);
  };

  const registers = (e) => {
    e.preventDefault();
    const pw = document.querySelector('#password');

    if(!check){
      alert('인증 먼저 해주세요');
      return;
    }else if (!pw) {
      alert('설정할 비밀번호를 입력해주세요.');
      pw.focus();
      return;
    } else if (pw.value.length < 4) {
      alert('재설정 비밀번호는 최소 4자 이상이어야 합니다.');
      pw.focus();
      return;
    }

    axios({
      method: 'post',
      url: '/api/findpw',
      data: {
        email,
        checkpw: pw.value,
      },
    })
      .then((res) => {
        if (res.data.success) navigate('/');
        else alert(res.data.message);
      })
      .catch((err) => console.log(err.message));
  };

  const signcheck = (e) =>{
    e.preventDefault();
    if(verificationCode === number){
      alert('인증 완료');
      setCheck(true);
    }else{
      alert('인증번호가 일치하지 않습니다 다시 입력해주세요');
    }
  }
  return (
    <div className="page">
      <form action="">
        <h1 id="sg">비번 찾기</h1>
        <div className="findpw">
          <input id="email" type="email" placeholder="Email" value={email}
            onChange={(e) => setEmail(e.target.value)}
            ref={emailInputRef}
          />
          <div className="paer">
            {isVerificationCodeVisible ? (
              <>
                <input id="chk-text" type="text" placeholder="인증번호" value={verificationCode}
                  onChange={(e) => setVerificationCode(e.target.value)}
                  style={{ width: '80%', marginLeft: '5px' }}
                />
                <input className="buttons" onClick={signcheck} type="button" value="인증 확인"
                  style={{ width: '150px', height: '40px', marginLeft: '10px', borderRadius: '10px' }}
                />
              </>
            ) : (
              <input className="buttons" type="button" value="인증하기"
                onClick={handleVerificationClick}
                disabled={!isVerificationEnabled}
                style={{ width: '200px', marginLeft: '105px' }}
              />
            )}
          </div>
          <div style={{ textAlign: 'center',marginBottom:'15px',marginTop:'10px' }}>재설정할 비밀번호</div>
          <input id="password" type="password" placeholder="password" />
          <button className="buttons" onClick={registers}>
            저장하기
          </button>
        </div>
      </form>
      <style jsx>{`

        .page{
          display: flex;
          justify-content: center;
          align-items: center;
          height: 95vh;
        }
        .findpw {
          width: 410px;
          padding: 20px;
        }
        #sg {
          text-align: center;
          font-size: 28px;
        }
        .findpw input {
          width: 100%;
          padding: 10px;
          margin-left:-10px;
          margin-bottom: 15px;
          border: 1px solid #ccc;
          border-radius: 5px;
          outline: none;
          font-size: 14px;
        }
        .paer{
          display:flex;
          align-items:center;
        }
        .round {
          position: absolute;
          margin-top:-52px;
          margin-left:275px;
          padding:8px 15px;
          background:#a428db;
          color:#fff;
          border-radius:10px;
          transition:background 0.2s ease-out;
        }
        .round:hover{ background:#7d1ac9; }

        .findpw input[type="email"]:focus, input[type="password"]:focus {
          border-color: #4c80f1;
        }

        .buttons {
          display: inline-block;
          padding: 0;
          margin-left:20px;
          width: 371px;
          height: 44px;
          background-color:#a428db;
          border: none;
          border-radius: 4px;
          font-size: 16px;
          font-weight: bold;
          color: #fff;
          text-align: center;
          text-decoration: none;
          cursor: pointer;
        }
        .buttons:hover { background-color: #7d1ac9; }
        .buttons:active {
          background-color: #7d1ac9;
          animation: particlesEffect 0.1s;
        }
        @keyframes particlesEffect {
          0%{transform: scale(1);}
          50%{transform: scale(1.03);}
          100%{transform: scale(1);}
        }
        `}</style>
    </div>
  );
}

export default Findpw; 