import NewNav from "./adminNav2";
import axios from "axios";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
const AdminMain = () =>{
  const navigate = useNavigate();
  useEffect(()=>{
    axios.post('/api/admin',{
      id: sessionStorage.getItem('isLogin')
    }).then((res)=>{
      if(res.data.success){
        navigate('/admin');
      }else{
        alert('비정상적인 집근입니다');
        navigate('/');
      }
    })
  },[])
  return(
    <div style={{
      marginTop: '120px'
    }}>
      <NewNav/>
      관리자 페이지 메인입니다.
    </div>
  )
}

export default AdminMain;