import React, { Component } from 'react';
import SunEditor from 'suneditor-react';
import 'suneditor/dist/css/suneditor.min.css';

class EditorComponent extends Component {

  editorRef = null;

  modules = {
    toolbar: [
      ['undo', 'redo'],
      ['font', 'fontSize', 'formatBlock'],
      ['fontColor', 'hiliteColor', 'textStyle'],
      ['bold','underline','italic','strike'],
      ['subscript','superscript','table','link','video'],
      ['image','imageGallery',],
      ['codeView'],
      ['removeFormat']
    ],  
  }

  // formats는 suneditor에서 지원하는 빌트인 formats 외에 사용자 정의 포맷을 지정할 수 있습니다.
  formats = ['font', 'fontSize', 'formatBlock', 'bold', 'underline', 'italic', 'strike', 'subscript', 'superscript', 'fontColor', 'hiliteColor', 'indent', 'outdent', 'list', 'table', 'link', 'image', 'video', 'fullScreen', 'showBlocks', 'codeView']

  // onMount 이벤트 핸들러
  handleEditorMount = (editor) => {
    this.editorRef = editor;
  }

  render() {
    const { value, onChange } = this.props;
    return (
      <div style={{
        marginTop:'20px'
      }}>
        <SunEditor height="400" width='103%'
          lang="ko" 
          setOptions={{ buttonList: this.modules.toolbar }}
          setContents={value}
          onChange={onChange}
          onMount={this.handleEditorMount}
          placeholder="내용을 입력해주세요"
        />
      </div>
    )
  }
}

export default EditorComponent;